import debug from 'debug';

const BASE = 'bdat';
const COLOURS = {
  trace: 'lightblue',
  info: 'purple',
  warn: 'orange',
  error: 'red'
};

//check for debug logs
//console.log(process.env.NODE_ENV);
//console.log(process.env.DEBUG);
//console.log(process.env.APP_RELEASE);

localStorage.removeItem('debug');
debug.disable();
if (process.env.NODE_ENV === 'development') {
  localStorage.setItem('debug', 'bdat:*');
  debug.enable('bdat:*');
}


class Log {

  constructor(){
    this.source = null;
  }

  init(source){
    this.source = source;
  }
  
  generateMessage(level, message) {
    // Set the prefix which will cause debug to enable the message
    const namespace = `${BASE}:${level}`;
    const createDebug = debug(namespace);
    
    // Set the colour of the message based on the level
    createDebug.color = COLOURS[level];
    
    if(this.source) { createDebug(this.source, message); }
    else { createDebug(message); }
  }
  
  trace(message) {
    return this.generateMessage('trace', message);
  }
  
  info(message) {
    return this.generateMessage('info', message);
  }

  warn(message) {
    return this.generateMessage('warn', message);
  }
  
  error(message) {
    return this.generateMessage('error', message);
  }
}

export default new Log();