/**
 * get lang Map
 * @param {*} langJson 
 */
export function getLangMap(langJson) {

    let langMap = new Map();
    let keys = Object.keys(langJson);
    for(let index = 0; index < keys.length; index++){
        let key = keys[index];
        if(null != key){
            langMap.set(key, langJson[key]["message"]);
        }
    }
    return langMap;
}

class BdLang {

    constructor() {
        this.langMap = null;
    }

    init(_langMap) {
        this.langMap = _langMap;
    }

    getString(message) {
        if (null != this.langMap) {
            if (this.langMap.has(message)) {
                return this.langMap.get(message);
            }
        }
        return chrome.i18n.getMessage(message);
    }
    
  }
  
  export default new BdLang();