import URI from 'urijs';

export default url => {

  if (null === url || '' === url) {
    return {
      schema: '',
      hostname: '',
      port: '',
      pathname: '',
      search: '',
      hash: ''
    };
  }

  try {
    var uri = new URI(url);
    return {
      schema: uri.protocol(),
      hostname: uri.hostname(),
      port: uri.port(),
      pathname: uri.pathname(),
      search: uri.search(),
      hash: uri.hash()
    };

  }  catch (ex) { }

  return {
    schema: '',
    hostname: '',
    port: '',
    pathname: '',
    search: '',
    hash: ''
  };
}
